.option-display-container {
  width: 300px;
  height: 300px;

  display: inline-block;

  border-radius: 100%;

  -webkit-perspective: 1000px;

          perspective: 1000px;
}

@media only screen and (min-width: 1000px) {
  .option-display-container {
    width: 450px;
    height: 450px;
  }
}

.flip-container {
  -webkit-box-shadow: 0px 5px 20px rgba(0,0,0,0.16);
          box-shadow: 0px 5px 20px rgba(0,0,0,0.16);
  width: 100%;
  height: 100%;
  border-radius: 100%;
}

@-webkit-keyframes flip-right {
    0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg) }
    33% { -webkit-transform: rotateY(190deg); transform: rotateY(190deg) }
    66% { -webkit-transform: rotateY(175deg); transform: rotateY(175deg) }
    100% { -webkit-transform: rotateY(179deg); transform: rotateY(179deg) }
}
@keyframes flip-right {
  0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg) }
  33% { -webkit-transform: rotateY(190deg); transform: rotateY(190deg) }
  66% { -webkit-transform: rotateY(175deg); transform: rotateY(175deg) }
  100% { -webkit-transform: rotateY(179deg); transform: rotateY(179deg) }
}

@-webkit-keyframes flip-left {
    0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg) }
    33% { -webkit-transform: rotateY(-190deg); transform: rotateY(-190deg) }
    66% { -webkit-transform: rotateY(-175deg); transform: rotateY(-175deg) }
    100% { -webkit-transform: rotateY(-179deg); transform: rotateY(-179deg) }
}
@keyframes flip-left {
  0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg) }
  33% { -webkit-transform: rotateY(-190deg); transform: rotateY(-190deg) }
  66% { -webkit-transform: rotateY(-175deg); transform: rotateY(-175deg) }
  100% { -webkit-transform: rotateY(-179deg); transform: rotateY(-179deg) }
}

.option-display-container
.animate-flip-right, .animate-flip-left {
  position: relative;

  -webkit-transform-style: preserve-3d;

          transform-style: preserve-3d;
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

.option-display-container
.animate-flip-right {
  -webkit-animation-name: flip-right;
          animation-name: flip-right;
}

.option-display-container
.animate-flip-left {
  -webkit-animation-name: flip-left;
          animation-name: flip-left;
}

.option-display-container
.front,
.option-display-container
.back {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;

  -webkit-backface-visibility: hidden;

          backface-visibility: hidden;
}

.option-display-container
.front{
  z-index: 1;
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
}

.option-display-container
.back{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.option-display-container
.display-image {
  width: 100%;
  max-width: 100%;
  border-radius: 100%;
}



